        </LineChart>
      </div>
      <div class="contents_container_inner_50">
        <div class="contents_title">
          Daily Write Count
        </div>
        <BarChart   :chartData="barChartData"
                    :chartOptions="barChartOptions"
                    :width="100"
                    :height="50">
        </BarChart>
      </div>
    </div>
  </div>
