<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);
// import DoughnutChart from './../chart/DoughnutChart.vue';
import LineChart from './../chart/LineChart.vue';
import BarChart from './../chart/BarChart.vue';

export default {
  props: [], 
  components: {
    // DoughnutChart,
    LineChart,
    BarChart,
  },
  data() {
    return {
      lineChartData: {},
      lineChartOptions : {},
      barChartData: {},
      barChartOptions : {},
      gaugeChartData: {},
      gaugeChartOptions : {},
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,

      imageSrc: null,
      imgDiaryCntnts: null,
      selectedFile: null,
      resultJson: {},
      returnMsg: "",
      tmpResponse: {},

      itemEValB: [],
      itemEValM: [],
      itemEValG: [],      
      newServerTestData: [],
      newServerTestDataErr: [],
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
    this.newServerTest();
    // this.getSummaryChartData();
    this.getDailyChartData();
  },
  methods: {
    newServerTest() {
      console.log('EmotionMain.vue : newServerTest : in ...');
      
      axios
        .post("/api/test", {})
        .then((response) => {  
          this.newServerTestData = response.data;
          console.log('newServerTest : this.newServerTestData : ', this.newServerTestData);
        })
        .catch((error) => {
          console.log(error);
          this.newServerTestDataErr = error
        });
    },
    getSummaryChartData() {
      console.log('EmotionMain.vue : getSummaryChartData : in ...');
      var resultJson = {};
      var chartData = {};
      axios
        .post("/api/item", {contentsId: 35, userId: 1})
        .then((response) => {  
          resultJson = response.data;
          chartData = resultJson[0].get_result_json
          this.setGaugeChartData(chartData);  // prepairing
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    // setGaugeChartData(chartData) {
    //   var chartDataLabels = [];
    //   var chartDataDatas = [];
    //   var chartBackgroundColor = [
    //     'rgb(192, 0, 0)',
    //       'rgb(253, 230, 89)',
    //       'rgb(67, 179, 52)',
    //   ];
          
    //   for(var i=0; i < chartData.length; i++) {
    //     chartDataLabels.push(chartData[i].emotion_nm);
    //     chartDataDatas.push(chartData[i].chart_data);
    //   }

    //   this.gaugeChartData = {
    //     labels:chartDataLabels, 
    //     datasets:[
    //       {
    //         label: '키워드분류건수',
    //         data: chartDataDatas, 
    //         backgroundColor: chartBackgroundColor,
    //         borderWidth: 1
    //               }
    //             ]
    //     },
    //   this.gaugeChartOptions = {
    //     maintainAspectRatio: true,   // 비율고정 해제
    //     rotation: -90,
    //     circumference: 180,
    //     legend: {
    //       display: false,
    //     },

    //   }
    // }, 
    getDailyChartData() {
      var resultJson = {};
      var chartData = {};
      axios
        .post("/api/item", {contentsId: 34, userId: 1})
        .then((response) => {  
          resultJson = response.data;
          chartData = resultJson[0].get_result_json[0]

          this.setEValueChartData(chartData.emotion_val_trend_daily);
          this.setECountChartData(chartData.emotion_cnt_daily);
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    
    setEValueChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];
          
      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.lineChartData = {
        labels:chartDataLabels, 
        datasets:[{
          label: 'emotionValue/Day',
          data: chartDataDatas, 
          scales: {
            y: {
                stacked: true
            }
          },
          backgroundColor: 'rgb(75, 192, 192)',
          // borderWidth: 3,
          fill: false,
          borderColor: 'rgb(75, 192, 192)',
          tension: 0.1
        }]
      }
      this.lineChartOptions = {
                            scales: {
                              x: {
                                grid: {
                                  display: true
                                },
                                ticks: {
                                  padding: 1
                                }
                              },
                              y: {
                                grid: {
                                  drawBorder: false,
                                  color: "lightgray",
                                  lineWidth: 1
                                },
                                min: -1,
                                max: 1,
                                // ticks: {
                                //   stepSize: 100,
                                //   padding: 10,
                                // }
                              }
                            },
                            maintainAspectRatio: true,   // 비율고정 해제
                          }
    }, 
    setECountChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];

      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.barChartData = {
          labels:chartDataLabels, 
          datasets:[{
            label: 'WriteCount/Day',
            data: chartDataDatas, 
            scales: {
              y: {
                  stacked: true
              }
            },
            // backgroundColor: chartBackgroundColor,
            // borderWidth: 1
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            tension: 0.1
          }]
          }
        this.barChartOptions = {
                              scales: {
                                x: {
                                  grid: {
                                    display: true
                                  },
                                  ticks: {
                                    padding: 1
                                  }
                                },
                                y: {
                                  grid: {
                                    drawBorder: false,
                                    color: "lightgray",
                                    lineWidth: 1
                                  },
                                  min: 0,
                                  // max: 10,
                                  // ticks: {
                                  //   stepSize: 100,
                                  //   padding: 10,
                                  // }
                                }
                              },
                              maintainAspectRatio: true,   // 비율고정 해제
          }
