<template>
  <!-- <div class="contents_item"> -->
    <canvas ref="barChart"></canvas>
    <!-- <canvas ref="barChart" :chartData="chartData" :chartOptions="chartOptions" :width="width" :height="height"></canvas> -->
    <div style="display:none;">
      {{ this.chartData }}
    </div>
  <!-- </div> -->
</template> 

<script>
  // import {Chart as ChartJS, registerables } from 'chart.js';
  // ChartJS.register(...registerables);

import { Chart, Title, Tooltip, Legend, BarController, BarElement, CategoryScale, LinearScale } from 'chart.js'
// ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)
Chart.register(Title, Tooltip, Legend, BarController, BarElement, CategoryScale, LinearScale)

// let chartStatus;
let myChart;
export default {
  props: {
      chartData: Object,
      chartOptions: Object,
      width: Number, 
      height: Number
    },
created() {
  console.log('BarChart : created : in ...');
},
  mounted() {
    console.log('BarChart : mounted : in...');
    this.renderChart();
  }, 
  methods: {
    renderChart() {
      myChart = new Chart(this.$refs.barChart, { 
                                                  type: 'bar', 
                                                  data: this.chartData, 
                                                  options: this.chartOptions,
                                                  width: this.width,
                                                  height: this.height,
                                                });
    },
    updateChart() {
      myChart.destroy();
      this.renderChart();
      // myChart.update();
    },
  },
  updated() {
    this.updateChart();
  },
};
</script>
  