<template>
  <div class="contents_container_inner">
    <div class="contents_container_inner">
      <!-- <img :src="require(`@/assets/images/emotion/bady.png`)" style="width: 50px; height: 45px;">
      <img :src="require(`@/assets/images/emotion/sosoy.png`)" style="width: 50px; height: 45px;">
      <img :src="require(`@/assets/images/emotion/goody.png`)" style="width: 50px; height: 45px;"> -->
    </div>

    <div class="contents_container_inner_70" style="height: 20rem;">
      <div class="contents_title">
        Emotional Ratio for the past month
      </div>
      <!-- <div class="contents_container_inner_50">
        <div>
          <DoughnutChart  :chartData="gaugeChartData"
                          :chartOptions="gaugeChartOptions"
                          :width="100"
                          :height="50">
          </DoughnutChart>
        </div>
      </div> -->
    </div>
    <div class="contents_container_inner_70" style="height: 20rem">
      <div class="contents_container_inner_50">
        <div class="contents_title">
          Daily Emotion Value Trend
        </div>
        <LineChart  :chartData="lineChartData"
                    :chartOptions="lineChartOptions"
                    :width="100"
                    :height="50">
        </LineChart>
      </div>
      <div class="contents_container_inner_50">
        <div class="contents_title">
          Daily Write Count
        </div>
        <BarChart   :chartData="barChartData"
                    :chartOptions="barChartOptions"
                    :width="100"
                    :height="50">
        </BarChart>
      </div>
    </div>
    <div class="contents_container_inner_70">
      <div class="contents_item_data">
        <table style="">
          <tr>
            <th>id</th>
            <th>contents</th>
            <th>feeling_val</th>
            <th>insert_dttm</th>
          </tr>
          <tr v-for="(feelingItem,id) in this.resultJson" :key="id">
            <td>{{feelingItem.id}}</td>
            <td>{{feelingItem.contents}}</td>
            <td>{{feelingItem.feeling_val}}</td>
            <td>{{feelingItem.insert_dttm}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>
  
<script>
const axios = require("axios");
import healingActivityJson from './../assets/json/healingActivityJson.json'; // select get_result_json(9);
// import DoughnutChart from './chart/DoughnutChart.vue';
import LineChart from './chart/LineChart.vue';
import BarChart from './chart/BarChart.vue';

export default {
  props: [], 
  components: {
    // DoughnutChart,
    LineChart,
    BarChart,
  },
  data() {
    return {
      lineChartData: {},
      lineChartOptions : {},
      barChartData: {},
      barChartOptions : {},
      gaugeChartData: {},
      gaugeChartOptions : {},
      dataHealingActivity: healingActivityJson,
      healingActivity: [],
      showContentYn: false,
      currentPage: 1,
      postsPerPage: 10,

      imageSrc: null,
      imgDiaryCntnts: null,
      selectedFile: null,
      resultJson: {},
      returnMsg: "",
      tmpResponse: {},

      itemEValB: [],
      itemEValM: [],
      itemEValG: [],      
    };
  },
  computed: {
    displayedPosts() {
      const startIndex = (this.currentPage - 1) * this.postsPerPage;
      const endIndex = startIndex + this.postsPerPage;
      return this.dataHealingActivity.slice(startIndex, endIndex);
    },
    totalPages() {
      return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
    }
  },
  created() {
    this.getSummaryChartData();
    this.getDailyChartData();
    this.getItemList()
  },
  methods: {
    getSummaryChartData() {
      var resultJson = {};
      var chartData = {};
      axios
        .post("/api/item", {contentsId: 35, userId: 1})
        .then((response) => {  
          resultJson = response.data;
          chartData = resultJson[0].get_result_json
          this.setGaugeChartData(chartData);
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    setGaugeChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];
      var chartBackgroundColor = [
        'rgb(192, 0, 0)',
          'rgb(253, 230, 89)',
          'rgb(67, 179, 52)',
      ];
          
      for(var i=0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].emotion_nm);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.gaugeChartData = {
        labels:chartDataLabels, 
        datasets:[
          {
            label: '키워드분류건수',
            data: chartDataDatas, 
            backgroundColor: chartBackgroundColor,
            borderWidth: 1
                  }
                ]
        },
      this.gaugeChartOptions = {
        maintainAspectRatio: true,   // 비율고정 해제
        rotation: -90,
        circumference: 180,
        legend: {
          display: false,
        },

      }
    }, 
    getDailyChartData() {
      var resultJson = {};
      var chartData = {};
      axios
        .post("/api/item", {contentsId: 34, userId: 1})
        .then((response) => {  
          resultJson = response.data;
          chartData = resultJson[0].get_result_json[0]

          this.setEValueChartData(chartData.emotion_val_trend_daily);
          this.setECountChartData(chartData.emotion_cnt_daily);
        })
        .catch((error) => {
          console.log(error);
        });
    }, 
    setEValueChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];
          
      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.lineChartData = {
        labels:chartDataLabels, 
        datasets:[{
          label: 'emotionValue/Day',
          data: chartDataDatas, 
          scales: {
            y: {
                stacked: true
            }
          },
          backgroundColor: 'rgb(75, 192, 192)',
          // borderWidth: 3,
          fill: false,
          borderColor: 'rgb(75, 192, 192)',
          tension: 0.1
        }]
      }
      this.lineChartOptions = {
                            scales: {
                              x: {
                                grid: {
                                  display: true
                                },
                                ticks: {
                                  padding: 1
                                }
                              },
                              y: {
                                grid: {
                                  drawBorder: false,
                                  color: "lightgray",
                                  lineWidth: 1
                                },
                                min: -1,
                                max: 1,
                                // ticks: {
                                //   stepSize: 100,
                                //   padding: 10,
                                // }
                              }
                            },
                            maintainAspectRatio: true,   // 비율고정 해제
                          }
    }, 
    setECountChartData(chartData) {
      var chartDataLabels = [];
      var chartDataDatas = [];

      for(var i = 0; i < chartData.length; i++) {
        chartDataLabels.push(chartData[i].insert_dt);
        chartDataDatas.push(chartData[i].chart_data);
      }

      this.barChartData = {
          labels:chartDataLabels, 
          datasets:[{
            label: 'WriteCount/Day',
            data: chartDataDatas, 
            scales: {
              y: {
                  stacked: true
              }
            },
            // backgroundColor: chartBackgroundColor,
            // borderWidth: 1
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            tension: 0.1
          }]
          }
        this.barChartOptions = {
                              scales: {
                                x: {
                                  grid: {
                                    display: true
                                  },
                                  ticks: {
                                    padding: 1
                                  }
                                },
                                y: {
                                  grid: {
                                    drawBorder: false,
                                    color: "lightgray",
                                    lineWidth: 1
                                  },
                                  min: 0,
                                  // max: 10,
                                  // ticks: {
                                  //   stepSize: 100,
                                  //   padding: 10,
                                  // }
                                }
                              },
                              maintainAspectRatio: true,   // 비율고정 해제
          }
    }, 
    getItemList() {
      axios
        .post("/api/item", {contentsId: 36, userId: 1})
        // axios.post(this.router, resultJsonImp)
        .then((response) => {  
          this.resultJson = response.data[0].get_result_json;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>