<template>
  <div>
    <div class="top_container">
      <TopArea  v-bind:menuItemData="menuItemData" 
                v-on:menuClicked="updateMenuPath"
                v-on:logoClicked="showRouterView"/>
    </div>
    <div class="contents_container" style="background-color: rgba(255,255,255,0.7);" v-if="this.showRouterViewYn">
      <div class="menu_path" style="margin-top: 5rem;">
        <!-- <router-link class="router_link" v-on:click="subMenuYn = false;" :to="{ path: '/dashboard' }"> -->
        <router-link class="router_link" v-on:click="subMenuYn = false;" :to="{ path: '/emotionMain' }">
        🏠 home / 
        </router-link>
        <router-link class="router_link" v-on:click="subMenuYn = false;" :to="{ path: menuSelectedPath[1] }">
          &nbsp;{{menuSelectedPath[0]}}&nbsp;
        </router-link>
        <router-link class="router_link" :to="{ path: menuSelectedPath[3] }" v-if="subMenuYn">
          /&nbsp;{{menuSelectedPath[2]}}
        </router-link>
      </div>
      <div class="contents_container_inner">
          <router-view />
      </div>
    </div>
    <div class="contents_container" style="padding-top: 5rem; background-color: rgba(255,255,255,0.7);" v-else>
      <!-- <DashBoard></DashBoard> -->
      <!-- <HomeMain></HomeMain> -->
      <EmotionMain></EmotionMain>
    </div>
    <div class="contents_container_full">
      <div class="bottom_container">
        Copyright<br>
          (주)떼알<br>
          Contact ✉ simatiga23@gmail.com
      </div>
    </div>
  </div>
<!-- <div class="back_to_top_btn" v-on:click="topFunction();">↑</div> -->
</template>

<script>
// import PopUpView from './components/ModalTest.vue';
import TopArea from './components/TopArea.vue';
// import DashBoard from './components/DashBoard.vue';
// import HomeMain from './components/HomeMain.vue';
import EmotionMain from './components/emotion/EmotionMain.vue';

import dataMenu from './assets/json/menu.json';  // select * from get_result_json(25, 1) as get_result_json;
var menuSelectedPath = [];

export default {
  name: 'App',
  // props: ['menuItemData'], 
  components: {
    // PopUpView,
    TopArea, 
    // DashBoard,
    // HomeMain,
    EmotionMain,
  },
  data() {
    return {
      menuItemData: dataMenu,

      menuSelectedPath,
      subMenuYn: false,
      showRouterViewYn: true,
      
      imgLogo: require("@/assets/images/common/logo_long_v1.jpg"),
      
      openModal: false,
      show: true
    }
  },
  created() {
    this.showRouterViewYn = false;
  },
  methods: {
    // updateMenuPath(selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter) {
    //   console.log(selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter);
    // }
    showRouterView() {
      this.showRouterViewYn = false;
    },
    updateMenuPath(selectedMainMenuNames, selectedMainMenuRouter, selectedSubMenuNames, selectedSubMenuRouter) {
      // console.log('in updateMenuPath');
      this.showRouterViewYn = true;
      // console.log('slectedMenuNames :: ', slectedMenuNames);
      this.menuSelectedPath[0] = selectedMainMenuNames;
      this.menuSelectedPath[1] = selectedMainMenuRouter;

      this.menuSelectedPath[2] = selectedSubMenuNames;
      this.menuSelectedPath[3] = selectedSubMenuRouter;

      if( selectedSubMenuNames == '') {
        this.subMenuYn = false;
      } else{
        this.subMenuYn = true;
      }
    },
    openModalView() {
      this.openModal = true;
    },
    closeModalView(data) {
      this.openModal = data
    },
  }, 
  updated() {
  },
}

</script>
<style>
@import '@/assets/style.css';
@import '@/assets/style_media.css';
</style>
